"use client";

import React, { useState } from "react";
import ProfileForm, { ProfileData } from "./ProfileForm";
import SkillsInput from "./SkillsInput";
import ResumeUpload from "./ResumeUpload";

export default function ProfilePage() {
  const [profile, setProfile] = useState<ProfileData>({
    firstName: "",
    lastName: "",
    phone: "",
    email: "",
    password: "",
    confirmPassword: "",
    skills: [],
    resume: "",
  });

  const handleSave = () => {
    console.log("Profile saved:", profile);
    alert("✅ Profile updated successfully!");
  };

  return (
    <div className="min-h-screen bg-gray-50 py-10 px-4">
      <div className="max-w-4xl mx-auto bg-white rounded-2xl shadow-lg p-8 border border-gray-100">
        <h1 className="text-3xl font-bold text-gray-800 mb-2 text-center">
          My Profile
        </h1>
        <p className="text-gray-500 text-center mb-8">
          Update your personal details, skills, and resume.
        </p>

        <div className="space-y-8">
          <ProfileForm profile={profile} setProfile={setProfile} />
          <SkillsInput profile={profile} setProfile={setProfile} />
          <ResumeUpload profile={profile} setProfile={setProfile} />
        </div>

        <div className="text-center mt-10">
          <button
            onClick={handleSave}
            className="px-8 py-3 bg-blue-600 text-white font-semibold rounded-lg hover:bg-blue-700 transition-all shadow-sm hover:shadow-md"
          >
            Save Changes
          </button>
        </div>
      </div>
    </div>
  );
}
